.container {
  width: 100%;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

.header {
  z-index: 10;
  background-color: #fff;
  justify-content: space-between;
  gap: 20px;
  padding-top: 15px;
  padding-bottom: 15px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.btn {
  color: #fff;
  cursor: pointer;
  background-color: #e60023;
  border-radius: 24px;
  justify-content: center;
  align-items: center;
  min-width: 100px;
  height: 48px;
  padding: 10px;
  font-size: 16px;
  font-weight: 500;
  display: flex;
}

.input__wrap {
  flex-grow: 1;
  position: relative;
}

.icon__search {
  width: 24px;
  height: 24px;
  position: absolute;
  top: 50%;
  left: 10px;
  transform: translateY(-50%);
}

.filter {
  background-color: #e9e9e9;
  border: none;
  border-radius: 24px;
  outline: none;
  width: 100%;
  height: 100%;
  padding-left: 40px;
  padding-right: 10px;
}

.images {
  margin-top: 70px;
}

.list {
  flex-wrap: wrap;
  gap: 20px;
  padding-top: 20px;
  padding-bottom: 20px;
  display: flex;
}

.thumb {
  width: 236px;
}

.img_wrap {
  position: relative;
}

.img {
  border-radius: 8px;
  margin-bottom: 20px;
}

.overlay {
  opacity: 0;
  background-color: #8e929880;
  border-radius: 8px;
  width: 100%;
  height: 100%;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 0;
  left: 0;
}

.img_wrap:hover .overlay, .img_wrap:focus .overlay {
  opacity: 1;
}

.img__btn {
  cursor: pointer;
  background-color: #fffc;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  display: flex;
  position: absolute;
  bottom: 5px;
  right: 5px;
}

.icon {
  fill: #636262;
  width: 24px;
  height: 24px;
  display: block;
}

.icon-dots {
  width: 18px;
}

.avatar__wrap {
  align-items: center;
  display: flex;
}

.avatar {
  border-radius: 50%;
  margin-right: 10px;
}

.description {
  width: 100%;
}

.img__menu {
  z-index: 1;
  background-color: #fff;
  border-radius: 16px;
  width: 236px;
  padding: 8px;
  position: absolute;
  top: 105%;
  left: 0;
  box-shadow: 0 0 8px #0000001a;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.menu__item:not(:last-child) {
  margin-bottom: 5px;
}

.menu__btn {
  cursor: pointer;
  background-color: #fff;
  border-radius: 8px;
  width: 100%;
  padding: 8px;
  font-weight: 600;
}

.menu__btn:hover, .menu__btn:focus {
  background-color: #e9e9e9;
}

.modal__board {
  width: auto;
}

.backdrop {
  opacity: 1;
  background-color: #000c;
  width: 100vw;
  height: 100vh;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
}

.modal__complain {
  background-color: #fff;
  border-radius: 24px;
  width: 540px;
  padding: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal__complain__title {
  text-align: center;
  margin-bottom: 20px;
  font-size: 24px;
  font-weight: 600;
}

.complain__item {
