}

.complain__item:not(:last-child) {
  margin-bottom: 20px;
}

.btn__wrap {
  flex-direction: row;
  justify-content: flex-end;
  gap: 10px;
  display: flex;
}

.complain__btn {
  cursor: pointer;
  border-radius: 24px;
  min-width: 60px;
  padding: 10px;
  font-weight: 600;
}

.complain__btn:hover, .complain__btn:focus {
  background-color: #e2e2e2;
}

.complain__text {
  margin-top: 10px;
  margin-left: 18px;
  font-size: 12px;
}

.choose__board {
  z-index: 1;
  background-color: #fff;
  border-radius: 16px;
  width: 134px;
  padding: 8px;
  position: fixed;
  top: 80px;
  right: 20px;
  box-shadow: 0 0 8px #0000001a;
}

.saved {
  opacity: 0;
  color: #fff;
  background-color: #000c;
  border-radius: 24px;
  padding: 10px;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 10px;
